<template>
    <div>
        <!-- 头部 -->
        <div class="cate-header">
            <a href="/"></a>
           <div class="cate-find"><i>发现</i><em>甄选家</em></div>
           <div class="cate-search"><i></i><em></em></div>
        </div>
        <!-- 子页面导航 -->
        <Transever :list='CateNavList' @click-Navigation='navAction'></Transever>
        <!-- 页面主要内容 -->
        
        <srcoll-itme id='cate' class="conten pages-botton">
            <h3 class="title-name" v-if="CateDetails.length != 0" >{{CateDetails[0].frontName}}</h3>
            <ul class="title-list">
                <li v-for="itme in CateDetails.slice(1)" :key='itme.id'>
                    <div class="img">
                        <div class="colorNum" v-if="itme.listPromBanner">
                            <i v-for="(i,ind) in itme.listPromBanner" :key='ind'>{{i.value}}</i>
                        </div>
                        <div class="colorNum simpleDesc" v-else>
                            <i>{{itme.simpleDesc}}</i>
                        </div>
                        <img v-lazy="itme.listPicUrl" alt="">
                    </div>
                    <!-- <div class="img-title">{{itme.simpleDesc}}</div> -->
                    <div class="itmeName">{{itme.name}}</div>
                    <div class="price">
                        <span class="counterPrice">￥{{itme.retailPrice}}</span>
                        <span class="retailPrice" v-if="itme.counterPrice">￥{{itme.counterPrice}}</span>
                    </div>
                    <div class="itemTagList"><i v-for="(i,ind) in itme.itemTagList" :key='ind'>{{i.name}}</i></div>
                </li>
            </ul>
        </srcoll-itme>
        
    </div>
</template>

<script>
import transverse from './childre/transverse';
import scroll from '../../../../components/scroll';

import { mapState , mapActions } from 'vuex'
export default {
    components:{
        [transverse.name]:transverse,
        [scroll.name]:scroll,
    },
    computed:{
        ...mapState({
            CateDetails:state=>state.Cate.CateDetails,
            CateNavList:state=>state.Cate.CateNavList,
            cateDetailsfalg:state=>state.Cate.cateDetailsfalg,
        }),
        url:function(){
            return this.$router.history.current.query;
        }
    },
    methods:{
        ...mapActions({
            requesCateDetails:'Cate/requesCateDetails',
            requesCateDetailsNav:'Cate/requesCateDetailsNav',
        }),
        navAction(id){
            let url = '/api/category/listmap/group/item?categoryId='+ this.url.categoryId +'&subCategoryId=' + ( id || this.url.subCategoryId );
            this.requesCateDetails(url);
        }
    },
    mounted(){
        if(this.cateDetailsfalg==true){
            this.navAction();
        };
        this.requesCateDetailsNav('/api/category/listmap/group?categoryId='+ this.url.categoryId); 
    },

    
    
}



</script>








<style lang="scss" scoped>
#cate{
    background: #fff;
    margin-top: 2vw;
}
.cate-header{
    width: 100%;
    display: flex;
    justify-content:space-between;
    height: 44px;
    line-height: 44px;
    background: #fff;
    a{
        width: 22px;
        height:80%;
        display: block;
        background: url('//yanxuan-static.nosdn.127.net/hxm/yanxuan-wap/p/20161201/style/img/sprites/hd-s9f33319f5a-b1aa4c35e6.png') no-repeat 0  -26px;
        background-size: 372%;
        margin-left: 5vw;
    }
    .cate-search{
        i,em{
            width: 22px;
            height:80%;
            margin-right: 5vw;
            display: inline-block;
            background: url('//yanxuan-static.nosdn.127.net/hxm/yanxuan-wap/p/20161201/style/img/sprites/hd-s9f33319f5a-b1aa4c35e6.png') no-repeat;
            background-size: 372%;
        }
        i{
            background-position:-5px -165px;
        }
        em{
            background-position:-5px 4px;
        }
    }
    .cate-find{
        flex: 1;
        text-align: center;
        i{
            font-size: 18px;
            font-weight: bold;
            color: #b4282d;;
        }
        em{
            display: inline-block;
            margin-left: 3vw;
            font-size: 14px;
            color: #7F7F7F;
        }
    }
}
.title-name{
    text-align: center;
    background: #fff;
    line-height: 14vw;
    font-size: 13px;
}
.title-list{
    width: 100%;
    box-sizing: border-box;
    padding: 5px;
    li{
        width: 50%;
        display: inline-block;
        box-sizing: border-box;
        padding:5px;
        vertical-align:top;
        .img{
            position: relative;
            width: 100%;
            background:  #f4f4f4;
            display: block;
            img{
                width: 100%;
            }
            .colorNum{
                position: absolute;
                bottom: 0;
                width: 100%;
                background: #FFE8CD;
                color: #F48F18;
                font-size: 12px;
                padding: 3px 0;
                text-indent: 2pt;
                white-space: nowrap;
                overflow: hidden;
                text-overflow: ellipsis;
            }
            .colorNum.simpleDesc{
                background: #F1ECE2;
                color: #9F8A60;
            }
        }
        .img-title{
            box-sizing: border-box;
            padding: 3px;
            border-radius:3px;
            width: 100%;
            background: #F1ECE2;
            color: #9F8A60;
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
        }
        .itmeName{
            white-space: nowrap;
            overflow: hidden;
            text-overflow: ellipsis;
            width: 100%;
            font-size: 14px;
            padding: 5px 0;
        }
        .price{
            font-size: 14px;
            padding: 5px 0;
            width: 100%;
            .counterPrice{
                color: #b4282d;
                font-size: 14px;
                display: inline-block;
                margin-right: 5px;
            }
            .retailPrice{
                color: #999;
                text-decoration: line-through;
            }
        }
        .itemTagList i{
            display: inline-block;
            padding: 2px 5px;
            color: #b4282d;
            font-size: 10px;
            border: 1px solid #b4282d;
            border-radius: 8px;
        }
        .itemTagList i:nth-of-type(2){
            border:none;
            background-image: url('http://yanxuan.nosdn.127.net/df03eaa6d957998728e3c425b900401e.png');
        }
            
    }
}

</style>

