<template>
    
    <div class="home-nav" ref='homenav'>
        <ul>
            <li v-for="(itme,index) in list"
            :id='itme.id'
            :class="{Action:index == CateNavIndex}"
            @click="chanActon(itme.id,index)"
            :key='itme.id'
            >{{itme.name}}</li>
        </ul>
    </div>
</template>

<script>
import BScroll from 'better-scroll';
import { mapState } from 'vuex';
export default {
    name:'Transever',
    props:{
        list:{
            type:Array,
            required:true
        },
        urlList:String,
    },
    data:function(){
        return {
            Scrolls:'',
        }
    } ,
    computed:{
        ...mapState({
            CateNavIndex:state=>state.Cate.CateNavIndex,
        }),
       
    },
    // watch:{
    //      CateNavIndex:function(){
            
    //     }
    // },
    methods:{
        chanActon(id,index){
            this.$emit('click-Navigation',id);
            this.$store.commit('Cate/setCateNavIndex',index);
        }
    },
    mounted(){
        this.Scrolls = new BScroll(this.$refs.homenav,{
            scrollX: true,
            click: true,
            tap:true
        });
        //while(this.list.length==0){};
        //  let i = this.CateNavIndex;
        //     console.log(this.list,i)
            // let id =  this.list[i].id;
            // let el = document.getElementById(id);
            // this.Scrolls.scrollToElement(el,200,true);
    }
}
</script>

<style lang="scss" scoped>
.home-nav{
    flex: 1;
    white-space: nowrap;
    overflow: hidden;
    background: #fff;
    color: #7F7F7F;
    ul{
        display: inline-block;
        height: 30px;
        position: relative;
        li{
            height: 100%;
            padding: 0 13px;
            box-sizing: border-box;
            display: inline-block;
            border-bottom: 2px solid transparent;
        }
        .Action{
            border-bottom: 2px solid #b4282d;
            color: #b4282d;
        }
    }
    
}
</style>

